.smallImg{
    width: 100%;
    max-width: 1200px;
}
.smallImgList{
    list-style: none;
        width: 100%;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    padding:0;
}
.smallImgListItem{
    width: calc(50% - 20px);
    padding-bottom: 30%;
    box-sizing: border-box;
    margin: 10px;
   position: relative;
   overflow: hidden;
}
.smallImgsListItemImg{
    width: 100%;
   height: 100%;
    object-fit: cover;    
    position: absolute;
}

@media screen and (min-width: 1200px) {
    .smallImgListItem{
        width: calc(25% - 20px);
        padding-bottom: 16%;
    }
    .smallImgsListItemImg{
        
        
        
    }}
