.contacts {
  display: flex;
  flex-direction: column;
  width: 100%;
  margin: 0 auto;
  box-sizing: border-box;
  justify-content: center;
  align-items: center;
  max-width: 1200px;
}
.contactsWrapper {
  display: flex;
  flex-direction: column;
  width: 100%;
  box-sizing: border-box;
  justify-content: center;
  align-items: center;
}
.contactInfo {
  max-width: 380px;
  display: flex;
  flex-direction: column;
  width: 100%;
  box-sizing: border-box;
  justify-content: center;
  align-items: center;
  padding: 0 20px;
}
.contactInfoList {
  list-style: none;
  display: flex;
  flex-direction: column;
  width: 100%;
  box-sizing: border-box;
  justify-content: center;
  align-items: center;
  padding: 0;
}
.contactInfoListItem {
}
.contactInfoContent {
  color: black;
  font-size: 25px;
  font-weight: 300;
  margin-bottom: 0;
}
a,
.contactInfoContent {
  color: black;
  text-decoration: none;
  font-size: 25px;
  font-weight: 300;
}
.contactInfoListItem {
}
.contactForm {
  max-width: 380px;
  padding: 0 20px 90px 20px;
  font-size: 20px;
  max-width: 500px;
}
.contactFormText {
  text-align: center;
}
.contactFormForm {
  display: flex;
  flex-direction: column;
}
.contactFormInput {
  margin-bottom: 10px;
  height: 60px;
  line-height: 1.3;
  border-radius: 0;

  outline: none;
  padding-left: 20px;
  border: 1px solid #c7c7c7;
}
.contactFormButton {
  background-color: #ffa600;
  color: white;
  border-radius: 5px;
  line-height: 3;
  border: none;
}
@media screen and (min-width: 700px) {
  .contactsWrapper {
    display: flex;
    flex-direction: row;
    width: 100%;
    box-sizing: border-box;
    justify-content: center;
    align-items: flex-start;
  }
  .contactInfo {
    align-items: flex-start;
  }
  .contactInfoList {
    align-items: flex-start;
  }
}
@media screen and (min-width: 1200px) {
  .contactInfoContent {
    font-size: 30px;
  }
  a,
  .contactInfoContent {
    font-size: 30px;
  }
  .contactInfo {
    max-width: 560px;
  }
  .contactForm {
    max-width: 560px;
  }
}
